<template>
    <div class="form-box">
        <el-form ref="form" :model="form" label-width="90px">
          <el-form-item label="现居地址">
            <el-input v-model="form.name" :placeholder="mention"></el-input>
          </el-form-item>  
          <el-form-item label="工作时间">
            <el-select v-model="form.region" placeholder="中间有一个30分钟调休">
              <el-option label=" 9:00 - 18:00" value=""></el-option>
              <el-option label=" 18:00 - 3:00" value=""></el-option>
            </el-select>
          </el-form-item> 
          <el-form-item
            style="width: 40%"
            label="年龄"
            prop="age"
            :rules="[
              { required: true, message: '年龄不能为空'},
              { type: 'number', message: '年龄必须为数字值'}
            ]"
          >
            <el-input type="age" v-model.number="numberValidateForm.age" autocomplete="off"></el-input>
          </el-form-item> 
          
          
          <el-form-item>
            <el-button type="primary" @click="submitForm('numberValidateForm')">提交</el-button>
            <el-button @click="resetForm('numberValidateForm')">重置</el-button>
          </el-form-item>       
        </el-form>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                form:{
                    name:'',
                    region:null,
                },
                numberValidateForm: {
                    age: ''
                },
                mention:'这只是一个单纯的表单而已,没有实现任何功能'
            }
        },
        methods:{
            submitForm(){
                
            },
            resetForm(){
                
            }
        }
    }
</script>

<style scoped="true">
    .form-box{
        box-sizing: border-box;
        width: 60%;
        height: 600px;
        margin: 30px auto;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
        padding: 20px;
    }
</style>


