import Vue from 'vue'
import Router from 'vue-router'
import page1 from '@/components/pages/page1'
import notFound404 from '@/components/pages/notfound'
import rider from '@/components/pages/rider'
import join from '@/components/pages/join'
import aboutPos from '@/components/pages/aboutpos'
Vue.use(Router)


export default new Router({
  routes: [
    {
        path: '/',
        name: 'page1',
        component: page1
    },
    {
        path:'/rider',
        component:rider
    },
    {
        path:'/join',
        component:join
    },
    {
        path:'/aboutpos',
        component:aboutPos
    },
    {
        path: '*',
        component:notFound404,
        name:'not404'
    },
  ]
})



