<template>
    <div class="main body">
       <div class="display">
        <header>
            <span class="page1-span">
                石阡必胜基外卖
            </span>
            <svg class="icon page1-icon" aria-hidden="true">
                <use xlink:href="#icon-xiaoji"></use>
            </svg>
            <span class="span-store" @click="toPay">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-gouwuche"></use>
                </svg>
                <p style="font-size: 14px">购物车</p>
            </span>
        </header>
        
        <div>
         <div class="welcome">
             <img :src="windowImg" alt="" style="width: 100%; height: 100%" title="这个世界需要更多的炸鸡!" alt="嗯?好像找不到了">
         </div>
          <div class="package">
              <h2>超值套餐</h2>
              <div class="package-box">
                  <div v-for="good in packages"> 
                      <p><span>{{good.name}}</span> : <span>{{good.price}}</span> 元</p> 
                      <div class="store">
                          <p @click="addStore">
                            <el-button type="primary">
                                <span>加入购物车</span>
                                <svg class="icon add-store-svg" aria-hidden="true" style="font-size: 30px;margin-left: 3px;">
                                    <use xlink:href="#icon-gouwuche"></use>
                                </svg> 
                            </el-button>
                          </p>
                          <el-input-number size="mini" v-model="good.count" :min="1" @change="countChange" class="count"></el-input-number>
                      </div>
                      <img :src='good.url' alt="" style="width:100%; height:100%">
                  </div>
              </div>
          </div>
          
          <div class="single">
              <h2>热卖单品</h2>
              <div class="package-box">
                  <div v-for="good in items"> 
                      <p><span>{{good.name}}</span> : <span class="money">{{good.price}}</span> 元</p> 
                      <div class="store">
                          <p @click="addStore">
                            <el-button type="primary">
                                <span>加入购物车</span>
                                <svg class="icon add-store-svg" aria-hidden="true" style="font-size: 30px;margin-left: 3px;">
                                    <use xlink:href="#icon-gouwuche"></use>
                                </svg> 
                            </el-button>
                          </p>
                          <el-input-number size="mini" v-model="good.count" :min="1" @change="countChange" class="count"></el-input-number>
                      </div>
                      <img :src='good.url' alt="" style="width:100%; height:80%">
                  </div>
              </div>
          </div> 
        </div>   
       </div>

        <div class="pay">
            <p>付款界面 <span class="span-close" @click="closePay">X</span></p>
            <p>总金额:{{store.totalprice}}</p>
            <div class="goods">
                <el-tag color="orange"  v-for="good in store.goods" style="margin: 5px;">{{good}}</el-tag>
            </div>
            <el-row>
                  <el-button type="primary" @click="pay">付款</el-button>
                  <el-button type="warning" class="clear-store" @click="clearStore">清空</el-button>
            </el-row>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                store:{
                    totalprice:0,
                    goods:[],
                    count:1
                },
                packages:[
                    {
                        name:'炸鸡拼盘',
                        price:48,
                        url:require('@/assets/vfc3.jpeg'),
                        count:0
                    },
                    {
                        name:'圣诞套餐',
                        price:88,
                        url:require('@/assets/vfc4.jpg'),
                        count:0
                    },
                    {
                        name:'单人鸡排饭',
                        price:26,
                        url:require('@/assets/vfc8.jpg'),
                        count:0
                    },
                    {
                        name:'单人套餐',
                        price:36,
                        url:require('@/assets/vfc9.jpg'),
                        count:0
                    },
                    {
                        name:'双人套餐',
                        price:66,
                        url:require('@/assets/vfc12.jpg'),
                        count:0
                    },
    
                ],
                items:[
                    {
                        name:'冰淇淋',
                        price:13,
                        url:require('@/assets/vfc1.jpg'),
                        count:0
                    },
                    {
                        name:'双层牛肉堡',
                        price:23,
                        url:require('@/assets/vfc13.jpeg'),
                        count:0
                    },
                    {
                        name:'老北京鸡肉卷',
                        price:15,
                        url:require('@/assets/vfc16.jpg'),
                        count:0
                    },
                    {
                        name:'美式咖啡',
                        price:18,
                        url:require('@/assets/vfc15.jpg'),
                        count:0
                    },
                    {
                        name:'单人薯条',
                        price:10,
                        url:require('@/assets/vfc14.jpg'),
                        count:0
                    },   
                ],
                num:0,
                windowImg:require('@/assets/window.jpg')
                
            }
        },
        methods:{
            addStore(e){
                //e.target.parentElement.parentElement.parentElement.childNodes[0].textContent
                let fatherNode = e.currentTarget.parentNode.parentElement;
                let price = Number(fatherNode.children[0].children[1].textContent);
                let good = fatherNode.children[0].children[0].textContent;
                this.store.totalprice += ( price* this.store.count);
                for(let i = 0 ; i < this.store.count;i++){
                    this.store.goods.push(good)
                }
                console.log(this.store.totalprice,this.store.goods,this.store.count);
                this.$emit('sendStore',this.store);
            },
            countChange(value){
                this.store.count = value
            },
            returnStore(){
                let _this = this
                return _this.store;
            },
            toPay(){
                const pay = document.querySelector('.pay');
                const display = document.querySelector('.display');
                pay.style.display = 'block';
                display.style.opacity = 0.5;
                
            },
            closePay(){
                const pay = document.querySelector('.pay');
                const display = document.querySelector('.display');
                pay.style.display = 'none';
                display.style.opacity = 1;
            },
            clearStore(){
                this.store.totalprice = 0;
                this.store.count = 0;
                this.store.goods = [];
            },
            pay(){
                alert('要是真的可以付款就好啦，哈哈')
            }
        },
        computed:{
                
        }
    }
</script>

<style scoped="true">
    .main{
        margin: 0;
        padding: 0;
    }
    .page1-icon{
        font-size: 50px;
    }
    .welcome{
        border: 1px solid;
        width: 80%;
        height: 500px;
        margin: 20px auto;
    }
    .page1-span{
        margin-left: 30px;
        font-size: 32px;
        letter-spacing: 4px;
        font-weight: 900;    
        text-shadow: 2px 2px 3px darkgrey;
    }
    header{
        background-color: lightblue;
        height: 80px;
        position: sticky;
        top: 0;
        z-index: 999;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
        line-height: 90px;
        min-width: 600px;
        
    }
    .pay{
        box-sizing: border-box;
        padding: 10px;
        position: absolute;
        width: 360px;
        height: 360px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        z-index:999;
        border-radius: 5%;
        display: none;
        box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
        background-color: blanchedalmond;
    }
    .span-close{
        font-size: 24px;
        position: absolute;
        right: 10px;
        top: 5px;
        cursor: pointer;
        padding: 5px;
    }
    .goods{
        height: 200px;
        overflow: auto;
    }
    .pay p{
        text-align: center;
    }
    .el-row{
        position: absolute;
        bottom: 10px;
        right: 10px;
        text-align: right;
    }
    .goods{

    }
    .span-store{
        font-size: 40px;
        line-height: 0px;
        position: absolute;
        right: 20px;
        top: 10px;  
    }
    .count{
        margin-left: 10px;
    }
    .store{
        border: 1px solid black;
        position: absolute;
        cursor: pointer;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        background-color: white;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
        padding: 5px;
        border-radius: 10%;
        display: none;
    }
    .el-button-group{
        float: right;
        margin-right: 20px;
        margin-top: 10px;
    }
    .el-carousel {
        margin-top: 20px;
    }
    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 400px;
        text-align: center;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
    .package{
        margin-top: 20px;
    }

    .package-box{
        display: flex;
        flex-wrap: wrap;
    }
    .package-box>div{
        width: 33.33% ;
        box-sizing: border-box;
        border:8px solid transparent;
        position: relative;
        
    }
    .package-box>div>p{
        
    }
    .package-box div:hover div{
        display: block;
    }
    .add-store-svg:hover{
        color: cornflowerblue;
    }
</style>























